/* ===== –ö–ê–†–£–°–ï–õ–¨ (–¶–ï–ù–¢–†) ===== */
$gap: 30px;
$btn-size: 44px;
$radius: 26px;

.extras__carousel,
.extras-carousel {
  grid-column: 2;
  position: relative;
  overflow: hidden;
  flex: 1; }

.carousel-track,
.extras-carousel__track {
  display: flex;
  gap: $gap;
  will-change: transform;
  transition: transform .4s ease; }

.carousel-track > img,
.extras-carousel__track > img,
.extras-carousel__slide {
  flex: 0 0 auto;
  width: 300px;
  height: 300px;
  border-radius: $radius;
  overflow: hidden;
  box-shadow: 0 6px 12px rgba(0,0,0,.15);
  display: block;
  object-fit: cover; }

.carousel-btn,
.extras-carousel__btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: $btn-size;
  height: $btn-size;
  border-radius: 50%;
  border: none;
  background-color: rgba(203, 255, 181, 0.90);
  color: $text-color;
  font-size: 24px;
  line-height: 1;
  display: grid;
  place-items: center;
  cursor: pointer;
  transition: .2s;
  z-index: 10;
  &:hover {
    background: darken($main-color-green, 15%);
    color: white; } }

.carousel-btn.prev,
.extras-carousel__btn--prev {
  left: 4px;
  padding: 0px 0px 0px 0px; }

.carousel-btn.next,
.extras-carousel__btn--next {
  right: 10px; }


/* === Extras: —Å–ª–µ–≤–∞ —Ç–µ–∫—Å—Ç+—Ü–µ–Ω–∞, —Å–ø—Ä–∞–≤–∞ –∫–∞—Ä—É—Å–µ–ª—å === */
.extras-section {
